import { ADD_TO_CART } from "../actionTypes";

const redux_add_to_cart = (item) => ({
  type: ADD_TO_CART,
  payload: item 
});
/* Actions typically take arguments and pass them as apyloads to the Reducer and are accesible in components */

const appActions = {  // Again here there is only one action, but its good to have this "combiner" set up so in the future I can
  redux_add_to_cart   // add more actions and have them all exported as one.
};

export default appActions;
