/*  If i was actually updating the store, or changing the state, the reducer would be like the instructions
for what the state should look like after an action takes place.

All I am doing here is console logging though. I am not changing the state.*/

export default function appReducer(state = initialState, action) { 
    switch (action.type) {
      case ADD_TO_CART:
        console.log(action.payload)
        return state;
    /* Here I would add more cases, for example - removeFromCart, changeItemAmount...
    I am pretty new to Redux, but switch cases seem very helpful here, so I wanted to follow what I saw as a convention.
    I realize a switch statemnet is overkill/ unnecesary here but I used it for the above mentioned reason - in the same way redux is
    overkill for this simple app - just to show I know how to use whatever. */
        
      default:
        return state;
  }
}
