const App = (props) => {
  return (
    <div>
    <Header />
    <ProductContainer {...props} />
    </div>
  );
};

const mapStateToProps = (state) => {  
  //console.log("state", state);   // returns object or updated object at any given time.
  return {
    list: state.appReducer.list
  };
}; //mapStateToProps takes the state of our store and passes it down for our app component.

const mapDispatchToProps = {
  ...appActions
}; // contains created app actions and passes them to our app compnent via props.

export default connect( 
  mapStateToProps,
  mapDispatchToProps
)(App);
/* connect is a curried function that essentially returns the App compnent with new functionality */