import { combineReducers } from "redux";
import appReducer from "./appReducer";

const rootReducer = combineReducers({
  appReducer
});

export default rootReducer;


//From what I have seen, having a root reducer is pretty standard when you have multple reducers.
//I realize I only have one reducer, but wanted to show whoever looks at this that I understand the standard practice,
// so I went ahead and just added it.